			this.phone = ''
			this.code = ''
			
		},
		// 获取验证码
		getCode(){
			// 防止重复获取
			if (this.codeTime>0){
				return;
			}
			// 验证手机号
			if (!this.validate()) return;
			// 请求数据
			this.$H.post('/user/sendcode',{
				phone:this.phone
			},{
				// 这样原生数据就会传输过来
				native:true
			}).then(res=>{
				// console.log(res)
				uni.showToast({
					title:res.data.msg,
					icon:'none'
				})
			}).catch(err=>{
				// console.log(err)
			})
			
			// 倒计时
			this.codeTime = 5
			// 箭头函数可以直接拿到外面的this的内容
			let timer = setInterval(()=>{
				if (this.codeTime >= 1){
					this.codeTime--
				} else{
					clearInterval(timer)
				}
			},1000)
			
		},
		// 表单验证
		validate(){
			//手机号正则 只是针对验证码的手机号字段，其他并未做兼容。
			var mPattern = /^1[34578]\d{9}$/; 
			if (!mPattern.test(this.phone)) {
				uni.showToast({
					title: '手机号格式不正确',
					icon: 'none'
				});
				return false
			}
			// ...更多验证
			return true
		},
		
