			// let url = ""
			let data = ""
			
			
			// 表单验证
			if(!this.status){
				if (!this.validate()) return;
			}
			{
				data = {
					phone:this.phone,
					code:this.code
				}
			}
			// if (this.status){
			// 	// 账号密码登录
			// 	url = '/user/login'
			// 	data = {
			// 		username:this.username,
			// 		password:this.password
			// 	}
			// }else{
				
			// 	// 手机验证码登录
			// 	// url = '/user/phonelogin'
			// 	data = {
			// 		phone:this.phone,
			// 		code:this.code
			// 		// phone:15020779433,
			// 		// code:123456
			// 	}
			// }
			
			// 提交后端
			this.$H.post(url,data).then(res=>{
				console.log("res==>",res)
				// 修改vuex的state，持久化存储
				this.$store.commit('login',res)
				// 提示和跳转
				uni.navigateBack({
